<template>
  <div id="map"></div>
</template>

<script>
import {
  importBMap
} from '@/utils/common'

const data = [
  ['单屋村村民委员会', '113.80430394539', '23.127495480074'],
  ['黄泗围村村民委员会', '113.8176039012', '23.120850765786'],
  ['城中社区居民委员会', '113.80890044899', '23.103725170579'],
  ['横滘村村民委员会', '113.77322734441', '23.085008617732'],
  ['鹤田厦村村民委员会', '113.75657067477', '23.07983712849'],
  ['梁家村村民委员会', '113.79028119782', '23.127205442911'],
  ['涌口村村民委员会', '113.8065283519', '23.125043162116'],
  ['唐洪村村民委员会', '113.83746329275', '23.111788001905'],
  ['桔洲村村民委员会', '113.79941104178', '23.116450495327'],
  ['西南村村民委员会', '113.81216737953', '23.122328972307'],
  ['四甲村村民委员会', '113.7894014673', '23.11055594129'],
  ['刘屋村村民委员会', '113.78562468592', '23.086434310956'],
  ['沙腰村村民委员会', '113.77699990807', '23.116793681861'],
  ['石碣村村民委员会', '113.8036252436', '23.095360060446'],
  ['水南村村民委员会', '113.82696835874', '23.107000745108']
]

export default {
  name: 'Map',
  data() {
    return {
    }
  },
  mounted() {
    importBMap().then(BMap => {
      function createMarker(point, title) {
        const marker = new BMap.Marker(point, {
          title,
          icon: new BMap.Icon(
            './icon/d31e52.png',
            new BMap.Size(32, 40)
          ),
          offset: new BMap.Size(0, -20),
          shadow: null
        })
        const label = new BMap.Label(title.slice(0, -5), {
          offset: new BMap.Size(30, 5)
        })
        marker.setLabel(label)
        return marker
      }
      const map = new BMap.Map('map', {
        enableMapClick: false
      })
      map.centerAndZoom(new BMap.Point(113.789677, 23.105473), 14)
      map.enableScrollWheelZoom()
      data.forEach((node, i) => {
        const marker = createMarker(
          new BMap.Point(node[1], node[2]),
          node[0],
        )
        marker.addEventListener('click', () => {
          this.$emit('node-click', {
            name: node[0],
            index: i + 1
          })
        })
        map.addOverlay(marker)
      })
    })
  },
  methods: {}
}
</script>

