            </div>
            <div class="grid-box__title">{{grid.name}}</div>
          </div>
          <div class="chart-md">
            <Row v-for="i in 3" :key="i" class="row--one-third">
              <Col v-for="j in 3" :key="j" :span="8" class="row--one-third__col">
                <div class="grid flexcol" :class="{ 'grid--light': (i + j) % 2 === 1 }">
                  <p class="grid__title"><img class="grid__icon" :src="`./icon/grid${i * 3 + j - 3}.png`" alt="">{{CELLS_LAYOUT[i * 3 + j - 4][0]}}</p>
