<style lang="scss" scoped>
.chart-lg {
  height: 500px;
}
.chart-md {
  height: 460px;
}
.row--one-third {
  height: 33.3%;
}
.row--one-third__col {
  padding: 10px;
  height: 100%;
}
.grid {
  border-radius: 5px;
  padding: 10px 0;
  height: 100%;
  justify-content: space-around;
  text-align: center;
  color: #fff;
  background: #5ab1ef;

  @at-root {
    .grid--light {
      background: #2ec7c9;
    }
    .grid__number {
      font-size: 30px;
    }
    .grid__icon {
      vertical-align: bottom;
      margin-right: 5px;
    }
    .grid__title {
      font-size: 20px;
      line-height: 24px;
    }
    .hr {
      margin: 5px 10%;
      border-color: #999;
    }
  }
}
.marker {
  position: relative;
  margin: 0 16px 0 10px;
  width: 32px;
  height: 40px;
  .marker__icon {
    position: absolute;
  }
  .marker__label {
    position: absolute;
    top: 8px;
    width: 32px;
    height: 15px;
    text-align: center;
    font-size: 15px;
    line-height: 1;
    font-weight: bold;
    color: #fff;
    font-family: arial, sans-serif;
  }
}
.grid-box__title {
  font-size: 20px;
  font-weight: bold;
}
</style>
